.spoiler {
  color: transparent;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  transition: all .4s;
  cursor: pointer;
  position: relative;
}

.spoiler:after {
  position: absolute;
  opacity: 0;
  content: 'Click to reveal spoiler';
  top: 45%;
  left: calc(50% - 100px);
  text-shadow: none;
  background: #222;
  color: #fff;
  display: inline-block;
  font-size: 13px;
  line-height: 1;
  padding: 2px 3px;
  width: 150px;
  font-family: Arial;
  text-align: center;
  border-radius: 3px;
  transition: all .4s;
}

.spoiler:hover:after {
  opacity: 1;
}

/** Lazy img **/
.lazyimg {
  display: block;
  border: 0 none;
  opacity: 1;
  transition: opacity .25s;
  background: #f2f2f2;
  outline: 0 none;
}

.lazyimg[data-src],
.lazyimg[data-srcset] {
  opacity: 0;
  transition: opacity .25s;
}
